.main-content{
    margin-top:155px;
    overflow-x: hidden;
  }
  
  .front-page{
    display: grid;
    transition: opacity 0.2s ease-out;
    grid-template-columns: 370px auto;
    height: calc((var(--vh,1vh)*100) -155px);
  }
  .sidebar{
    max-height: 100%;
    text-align: center;
  
  }
  .sidebar .box2{
    width:100%;
    height: 90px;
    border-bottom: 2px solid #1E1E1E;
  }
  .box1{
    position: relative;
    width:100%;
    height: 200px;
    border-bottom: 2px solid #1E1E1E;
  }
  .box1 span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    letter-spacing: 0.5em;
    font-size: 23px;
    text-align: left;
    width: 56%;
  
  }
  .box2{
    position: relative;
  }
  .box2 span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    letter-spacing: 0.5em;
    font-size: 18px;
  }
  
  .front-main{
    grid-column: span 1;
    max-height: 660px;
    border-left: 2px solid #1E1E1E;
    border-bottom: 2px solid #1E1E1E;
    overflow-x: hidden;
  }
  .project-content{
    position: relative;
    overflow: hidden;
  }
  
  .row-1{
    padding: 0 20px;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  
  .content-name{
    margin: 10px auto;
  }
  .wrapper{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    border-top: 1px solid rgb(117, 114, 114);
    grid-auto-rows: minmax(50px, auto);
  }
  .row{
    display: contents;
  }
  .row div {
    border-left: 1px solid rgb(117, 114, 114);
    border-bottom: 1px solid rgb(117, 114, 114);
  }
  .box3{
    height: 220px;
  }
  .box4{
    height: 50px;
  }