
*{
  font-family: 'Hiragino Mincho Pro';
}

.site-header{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  z-index: 9999;
  height: 155px;
  border-bottom: 2px solid #1E1E1E;
  background-color: #fff;
}
.header-content{
  height: 100%;
}
.footer-content{
  background-color: #ececec;
}
.footer-content div{
  position: relative;
  height: 80px;
}
.footer-content div span{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  letter-spacing: 0.5em;
  text-align: center;
  font-size: 13px;
}
.site-name{
  display: inline-block;
  height: 100%;
  line-height: 1;
}
.site-logo{
  position: absolute;
  right: 0;
  top: 0;
  width: 155px;
  height: 155px;
  border-left: 2px solid #1E1E1E;
}
